import React, { useState } from 'react'
import { Row, Col, Label, Button } from 'reactstrap'
import Select, { components } from 'react-select'
import { Send } from 'react-feather'
import Avatar from '@components/avatar'

const opponents = [
  {
    label: 'Online Opponents',
    options: [
      {
        value: 'jeff393',
        label: 'jeff393',
        isOnline: true,
        avatarURL: 'https://firebasestorage.googleapis.com/v0/b/esportsref-94707.appspot.com/o/avatars%2Favatar_01.png?alt=media&token=763c6c06-c56f-41a9-9fcf-09f72913c2f1',
        uid: 'RsMbrEh7GEh60YagY5TZ34W9EFA2'
      }
    ]
  },
  {
    label: 'Offline Opponents',
    options: [
      {
        value: 'topdev328',
        label: 'topdev328',
        isOnline: false,
        avatarURL: 'https://firebasestorage.googleapis.com/v0/b/esportsref-94707.appspot.com/o/avatars%2Favatar_05.png?alt=media&token=9cbedf7e-043f-4e2b-981e-adb74b08570f',
        uid: 'wL6jAzYWn0aWbx3yWwSrVEpXneE3'
      }
    ]
  }
]

const OptionComponent = ({ data, ...props }) => {
  const { label, avatarURL, isOnline } = data

  return (
    <components.Option {...props}>
      <Avatar
        className='box-shadow-1 mr-1'
        img={avatarURL}
        status={isOnline ? 'online' : 'offline'}
      />
      <span>{label}</span>
    </components.Option>
  )
}

const OpponentSelection = ({ handleSendInvitation }) => {
  const [selectedUserId, setSelectedUserId] = useState('')

  const handleChange = ({ uid }) => {
    setSelectedUserId(uid)
  }

  return (
    <>
      <Label>Choose your opponent</Label>
      <Select
        options={opponents}
        className='react-select'
        classNamePrefix='select'
        components={{
          Option: OptionComponent
        }}
        isLoading={true}
        onChange={handleChange}
      />
      <Button color='primary' className='mt-1' block onClick={() => handleSendInvitation(selectedUserId)}>
        <Send size={14} className='mr-1' />
        <span>Send Invitation</span>
      </Button>
    </>
  )
}

export default OpponentSelection