import { useEffect, useState } from 'react'
import { Container, Row, Col, Card, CardHeader, CardTitle, CardBody } from 'reactstrap'
import Measure from 'react-measure'

import CardChat from '../../components/CardChat'
import UserStatusCard from '../../components/UserStatusCard'

// ** Contracts
import ChildHPBContract from '@contracts/ChildHPBContract'

// ** Utility
import { isMetaMaskInstalled, switchToHPBNetwork } from '@utility/metamask'
import { auth as fireAuth } from '@utility/firebase'

// ** Firebase
import { createInvite } from '@services/firebase'

// ** Use Wallet
import { useWallet } from 'use-wallet'

// ** Store & Actions
import { useSelector, useDispatch } from 'react-redux'

// **
import OpponentSelection from './OpponentSelection'
import URLCopyBoard from './URLCopyBoard'

// GameSteps
import GameReady from './GameSteps/GameReady'
import SetContract from './GameSteps/SetContract'

// Game status constants
const GAME_CREATED = 'created'
const GAME_INVITING = 'inviting'
const GAME_ACCEPTED = 'accepted'
const GAME_PLAYER1_READY = 'player1-ready'
const GAME_PLAYER2_READY = 'player2-ready'
const GAME_ON_CONTRACT = 'on_contract'
const GAME_ABANDONED = 'abandoned'
const GAME_SET_CONTRACT = 'set_contract'
const GAME_FIRST_DEPOSITED = 'first_deposited'
const GAME_WAIT_REFEREE = 'wait_for_referee'
const GAME_PLAY_READY = 'ready_to_play'
const GAME_P1_READY = 'p1_ready'
const GAME_P2_READY = 'p2_ready'
const GAME_PLAYING_NOW = 'playing_now'
const GAME_ONE_CONCEDE = 'one_concede'
const GAME_REF_JUDGED = 'referee_judged'
const GAME_UNDER_DISPUTE = 'under_dispute'
const GAME_ADMINS_CHECKING = 'admins_checking'
const GAME_DISPUTE_RESOLVED = 'dispute_resolved'
const GAME_PAYOUT_WINNER = 'payout_winner'
const GAME_FINISHED = 'finished'

const REMAINING_TIME_TO_DISPUTE = 10 * 60 * 1000 // 10 minutes, after hpb wil be award to winner
const REMAINING_TIME_TO_ACCEPT = 3 * 60 * 1000 // 3 minutes, after the game will be abandoned
const REMAINING_TIME_TO_DEPOSIT = 5 * 60 * 1000 // 5 minutes
const REMAINING_TIME_TO_RESELECT_REFEREE = 2 * 60 * 1000 // 2 minutes

const HPB = (props) => {
  const { tokenContractAddress } = props.match.params
  const wallet = useWallet()

  const auth = useSelector(state => state.auth)
  const dispatch = useDispatch()

  const [height, setHeight] = useState(0)
  const [childHPBContract, setChildHPBContract] = useState(null)
  const [userId, setUserId] = useState('')
  const [currentGameStep, setCurrentGameStep] = useState(1)

  // check MetaMask
  useEffect(() => {
    if (isMetaMaskInstalled()) {
      const { balance } = wallet
      if (parseInt(balance) === -1) {
        switchToHPBNetwork().then(res => {
          if (res && res.status === 200) {
            setTimeout(() => wallet.connect('injected'), 500)
          }
        })
      }
    } else {
      alert('Please install MetaMask first.')
    }
  }, [])

  // create child hpb contract
  useEffect(() => {
    const { account } = wallet

    if (account) {
      const _childHPBContract = new ChildHPBContract({ 
        ChildHPBContractAddress : tokenContractAddress, 
        account
      })
      setChildHPBContract(_childHPBContract)
    }
  }, [wallet])

  // set user id
  useEffect(() => {
    setUserId(fireAuth.currentUser?.uid)
  }, [fireAuth])

  // handle send invitation
  const handleSendInvitation = (selectedUserId) => {
    if (!selectedUserId) {
      alert('Please select your opponent first.')
      return
    }

    createInvite({ 
      tokenType: 'Xdai',
      tokenContractAddress,
      player1: userId,
      player1Username: auth.user.username,
      player2: selectedUserId
    })
  }

  // game steps
  const renderSteps = () => {
    switch (currentGameStep) {
      case 1:
        return <GameReady />
      case 2:
        return <SetContract />
      default:
        return <p className='text-center font-md'>Pending . . .</p>
    }
  }

  return (
    <Row className="content-body">
      <Container style={{ maxWidth: '1200px' }}>
        <Row>
          <Col md={2} className="mb-1 mb-md-0">
            <Measure bounds
              onResize={contentRect => {
                setHeight(parseInt(contentRect.bounds.height))
              }}>
              {({ measureRef }) => (
                <div ref={measureRef} className='user-status-group row px-1 px-sm-0'>
                  <Col xs={4} md={12} className='px-0 px-sm-1'>
                    <UserStatusCard {...auth.user} />
                  </Col>
                  <Col xs={4} md={12} className='my-md-2 px-0 px-sm-1'>
                    <UserStatusCard {...auth.user} />
                  </Col>
                  <Col xs={4} md={12} className='px-0 px-sm-1'>
                    <UserStatusCard {...auth.user} />
                  </Col>
                </div>
              )}
            </Measure>
          </Col>
          <Col md={6}>
            <CardChat height={height} user={{ username: auth.user.username, avatarURL: auth.user.avatarURL, roomId: tokenContractAddress }} />
          </Col>
          <Col md={4}>
            <Card style={{ minHeight: height }} className='game-steps-widget'>
              <CardHeader>
                <div className='d-flex align-items-center' style={{height: 34}}>
                  <h3 className='mb-0 font-weight-bold'>Game Steps</h3>
                </div>
              </CardHeader>
              <CardBody className='rounded-bottom'>
                <Row>
                  <Col className='mt-1'>
                    <OpponentSelection handleSendInvitation={handleSendInvitation} />
                  </Col>
                </Row>
                <Row className='mt-2'>
                  <Col>
                    <URLCopyBoard url={`https://esrdapp.herokuapp.com/xdai/${tokenContractAddress}`} />
                  </Col>
                </Row>
                <Row className='mt-5'>
                  <Col>
                    {renderSteps()}
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </Row>
  )
}

export default HPB
