// Web3
import web3 from '@utility/web3'

// ** Abi
import ChildHPBContractABI from '@abis/ChildHPBContractABI.json'


class ChildHPBContract {

  constructor({ ChildHPBContractAddress, account }) {
    this.childHPBContract = new web3.eth.Contract(
      ChildHPBContractABI,
      ChildHPBContractAddress
    )
    this.account = account
  }

  // player ready
  async readyToCompete() {
    let resObj = {}

    try {
      await this.childHPBContract.methods.readyToCompete().send({
        from: this.account
      })

      resObj = {
        success: true,
        message: 'Ready to compete confirmation recorded'
      }
    } catch (error) {
      resObj = {
        success: false,
        message: error.message
      }
    }

    return resObj
  }

  // referee takes control
  async refTakesControl() {
    let resObj = {}

    try {
      await this.childHPBContract.methods.refTakescontrol().send({
        from: this.account
      })

      resObj = {
        success: true,
        message: 'Referee has now taken control'
      }
    } catch (error) {
      resObj = {
        success: false,
        message: error.message
      }
    }

    return resObj
  }

  // first deposit
  async firstDeposit({ name, amount }) {
    let resObj = {}

    try {
      await this.childHPBContract.methods.firstDeposit(name).send({
        from: this.account,
        value: web3.utils.toWei(amount, 'ether')
      })

      resObj = {
        success: true,
        message: 'First deposit successful'
      }
    } catch (error) {
      resObj = {
        success: false,
        message: error.message
      }
    }

    return resObj
  }

  // second deposit
  async secondDeposit(amount) {
    let resObj = {}

    try {
      await this.childHPBContract.methods.secondDeposit().send({
        from: this.account,
        value: web3.utils.toWei(amount, 'ether')
      })

      resObj = {
        success: true,
        message: 'Second deposit successful'
      }
    } catch (error) {
      resObj = {
        success: false,
        message: error.message
      }
    }

    return resObj
  }

  // referee added
  async refereeAdded() {
    let resObj = {}

    try {
      await this.childHPBContract.methods.refAddedToContract().send({
        from: this.account
      })

      resObj = {
        success: true,
        message: 'Referee has been added to contract'
      }
    } catch (error) {
      resObj = {
        success: false,
        message: error.message
      }
    }

    return resObj
  }

  // player for feit (give up)
  async playerForFeit() {
    let resObj = {}

    try {
      await this.childHPBContract.methods.player_forfeit().send({
        from: this.account
      })

      resObj = {
        success: true,
        message: 'Player has conceded'
      }
    } catch (error) {
      resObj = {
        success: false,
        message: error.message
      }
    }

    return resObj
  }

  // set winner A
  async setWinnerA() {
    let resObj = {}

    try {
      await this.childHPBContract.methods.setWinnerPlayerA().send({
        from: this.account
      })

      resObj = {
        success: true,
        message: 'Referee declares player 1 the winner'
      }
    } catch (error) {
      resObj = {
        success: false,
        message: error.message
      }
    }

    return resObj
  }

  // set winner draw
  async setWinnerDraw() {
    let resObj = {}

    try {
      await this.childHPBContract.methods.setResultDraw().send({
        from: this.account
      })

      resObj = {
        success: true,
        message: 'Referee declares the game a draw'
      }
    } catch (error) {
      resObj = {
        success: false,
        message: error.message
      }
    }

    return resObj
  }

  // set winner B
  async setWinnerB() {
    let resObj = {}

    try {
      await this.childHPBContract.methods.setWinnerPlayerB().send({
        from: this.account
      })

      resObj = {
        success: true,
        message: 'Referee declares player 2 the winner'
      }
    } catch (error) {
      resObj = {
        success: false,
        message: error.message
      }
    }

    return resObj
  }

  // dipute result
  async disputeResult() {
    let resObj = {}

    try {
      await this.childHPBContract.methods.disputeResult().send({
        from: this.account
      })

      resObj = {
        success: true,
        message: 'Player has disputed the result'
      }
    } catch (error) {
      resObj = {
        success: false,
        message: error.message
      }
    }

    return resObj
  }

  // payout winner
  async payoutWinner() {
    let resObj = {}

    try {
      await this.childHPBContract.methods.payoutWinner().send({
        from: this.account
      })

      resObj = {
        success: true,
        message: 'Winner has been paid'
      }
    } catch (error) {
      resObj = {
        success: false,
        message: error.message
      }
    }

    return resObj
  }
  
}

export default ChildHPBContract