import React, { useState } from 'react'
import { Form, Label, Input, Button } from 'reactstrap'
import FormGroup from 'reactstrap/lib/FormGroup'

export default function SetContract() {
  const [buttonText, setButtonText] = useState('Submit')
  const [disabled, setDisabled] = useState(false)

  const handleReady = () => {
    setDisabled(true)
    setButtonText(`Submited`)
  }

  return (
    <Form>
      <FormGroup>
        <Label>Game name</Label>
        <Input value={''} />
      </FormGroup>
      <FormGroup>
        <Label>xDai amount</Label>
        <Input value={''} />
      </FormGroup>
      <Button.Ripple color='danger' className='font-md mt-1' block onClick={handleReady} disabled={disabled}>{buttonText}</Button.Ripple>
    </Form>
  )
}