import { nanoid } from 'nanoid'

// ** Utility
import { firestore } from '@utility/firebase'

const db = firestore.collection('invites')

const isInviteExist = async ({ player1, player2 }) => {
  const snapshot = await db.where('status', '==', 'created').where('player1', '==', player1).where('player2', '==', player2).get()
  return !snapshot.empty
}

const createInvite = async ({ tokenType, tokenContractAddress, player1, player1Username, player2 }) => {

  if (await isInviteExist({ player1, player2 })) {
    return
  }

  db.doc(nanoid()).set({
    tokenType,
    tokenContractAddress,
    player1,
    player1Username,
    player2,
    status: 'created'
  })
}

export { createInvite }