import React, { useState } from 'react'
import { Button } from 'reactstrap'

export default function GameReady({ playerNumber = 1 }) {
  const [buttonText, setButtonText] = useState('I am ready')
  const [disabled, setDisabled] = useState(false)

  const handleReady = () => {
    setDisabled(true)
    setButtonText(`Player ${playerNumber} is ready`)
  }

  return (
    <>
      <Button.Ripple color='danger' className='py-1 font-lg' block onClick={handleReady} disabled={disabled}>{buttonText}</Button.Ripple>
    </>
  )
}