import classnames from 'classnames'
import Avatar from '@components/avatar'
import { useState, useEffect } from 'react'
import PerfectScrollbar from 'react-perfect-scrollbar'
import { MoreVertical, Send, Image } from 'react-feather'
import { Card, CardHeader, Form, Label, InputGroup, InputGroupAddon, Input, InputGroupText, Button } from 'reactstrap'
import { useDocumentData } from 'react-firebase-hooks/firestore'

// ** Utility
import { firestore } from '@utility/firebase'

import '@styles/base/pages/app-chat-list.scss'

const data = {
  chat: {
    id: 2,
    userId: 1,
    unseenMsgs: 0,
    chat: [
      {
        message: "How can we help? We're here for you!",
        senderId: 11
      },
      {
        message: 'Hey John, I am looking for the best admin template. Could you please help me to find it out?',
        senderId: 1
      },
      {
        message: 'It should be Bootstrap 4 compatible.',
        senderId: 1
      },
      { 
        message: 'Absolutely!', 
        senderId: 11 
      }
    ]
  },
  contact: {
    id: 1,
    fullName: 'Felecia Rower',
    avatar: 'https://firebasestorage.googleapis.com/v0/b/esportsref-94707.appspot.com/o/avatars%2Favatar_04.png?alt=media&token=fd2d584c-2f6c-4b21-930c-355b190d765f',
    status: 'away'
  }
}


const CardChat = (props) => {
  const { username, avatarURL, roomId } = props
  const currentChatRef = firestore.doc(`chats/${roomId}`)

  const [msg, setMsg] = useState('')
  const [chatRef, setChatRef] = useState(null)
  const [chatData, setChatData] = useState(data)
  
  const [value] = useDocumentData(currentChatRef)
  console.log(value)

  //** Formats chat data based on sender
  const formattedChatData = () => {
    let chatLog = []
    if (chatData) {
      chatLog = chatData.chat.chat
    }

    const formattedChatLog = []
    let chatMessageSenderId = chatLog[0] ? chatLog[0].senderId : undefined
    let msgGroup = {
      senderId: chatMessageSenderId,
      messages: []
    }
    chatLog.forEach((msg, index) => {
      if (chatMessageSenderId === msg.senderId) {
        msgGroup.messages.push({
          msg: msg.message,
          time: msg.time
        })
      } else {
        chatMessageSenderId = msg.senderId
        formattedChatLog.push(msgGroup)
        msgGroup = {
          senderId: msg.senderId,
          messages: [
            {
              msg: msg.message,
              time: msg.time
            }
          ]
        }
      }
      if (index === chatLog.length - 1) formattedChatLog.push(msgGroup)
    })
    return formattedChatLog
  }

  //** Renders user chat
  const renderChats = () => {
    return formattedChatData().map((item, index) => {
      return (
        <div
          key={index}
          className={classnames('chat', {
            'chat-left': item.senderId !== 11
          })}
        >
          <div className='chat-avatar'>
            <Avatar
              className='box-shadow-1'
              img={item.senderId === 11 ? avatarURL : chatData.contact.avatar}
            />
          </div>

          <div className='chat-body'>
            {item.messages.map(chat => (
              <div key={chat.msg} className='chat-content'>
                <p>{chat.msg}</p>
              </div>
            ))}
          </div>
        </div>
      )
    })
  }

  //** Scroll to chat bottom
  const scrollToBottom = () => {
    chatRef.scrollTop = Number.MAX_SAFE_INTEGER
  }

  useEffect(() => {
    if (chatRef !== null) {
      scrollToBottom()
    }
  }, [chatRef, chatData.chat.chat.length])

  const handleSendMsg = e => {
    e.preventDefault()
    if (msg.trim().length) {
      const newMsg = chatData

      newMsg.chat.chat.push({
        message: msg,
        time: new Date(),
        senderId: 11
      })

      setChatData(newMsg)
      setMsg('')
    }
  }

  return (
    <Card className='chat-widget mb-1'>
      <CardHeader>
        <div className='d-flex align-items-center' style={{height: 34}}>
          <h3 className='mb-0 font-weight-bold'>Discussion</h3>
        </div>
      </CardHeader>
      <div className='chat-app-window'>
        <PerfectScrollbar
          containerRef={el => setChatRef(el)}
          className='user-chats scroll-area'
          options={{ wheelPropagation: false }}
          style={ props.height > 500 ? { height: `calc(${props.height}px - 34px - 1.6rem - 56px )` } : { height: '78vh' } }
        >
          <div className='chats'>{renderChats()}</div>
        </PerfectScrollbar>
        <Form className='chat-app-form' onSubmit={e => handleSendMsg(e)}>
          <InputGroup className='input-group-merge mr-1 form-send-message'>
            <Input
              value={msg}
              className='border-0'
              onChange={e => setMsg(e.target.value)}
              placeholder='Type your message'
            />
          </InputGroup>
          <Button className='send' color='primary'>
            <Send size={14} />
          </Button>
        </Form>
      </div>
    </Card>
  )
}

export default CardChat
