import React, { useState } from 'react'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import { Label, Input, Button } from 'reactstrap'
import { Copy } from 'react-feather'

export default function URLCopyBoard({ url }) {
  const [buttonText, setButtonText] = useState('Copy URL to Clipboard')

  const onCopy = () => {
    setButtonText('URL Copied to Clipboard')
    setTimeout(() => {
      setButtonText('Copy URL to Clipboard')
    }, 1500)
  }

  return (
    <>
      <Label>Alternatively, you can send someone the following URL if you want them to join you.</Label>
      <Input value={url} editable={false} />
      <CopyToClipboard onCopy={onCopy} text={url}>
        <Button.Ripple color='warning' className='mt-1' block>
          <Copy size={14} className='mr-1' />
          <span>{buttonText}</span>
        </Button.Ripple>
      </CopyToClipboard>
    </>
  )
}